import { cloneDeep, assign } from 'lodash';

// 
const defaultState = {
    userInfo: {},
    pdfUrl: '',
    temporaryReason: [], // 
    temporaryRes: {}, // 
    resultData: {}
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setUserInfo (state, payload) {
            state.userInfo = payload;
        },
        setPdfUrl (state, payload) {
            state.pdfUrl = payload;
        },
        setTemporaryReason (state, payload) {
            state.temporaryReason = payload.map((item, index) => {
                return {
                    type: Object.keys(item).join(''),
                    value: Object.values(item).join('')
                };
            });
        },

        setTemporaryRes (state, payload) {
            // let arr = [];
            // arr.push({label: '', payloadue: payload.xm});
            // arr.push({label: '', payloadue: payload.xb});
            // arr.push({label: '', payloadue: payload.hzxm});
            // arr.push({label: '', payloadue: payload.yhzgx});
            // arr.push({label: '', payloadue: payload.hh});
            // arr.push({label: '', payloadue: payload.gmsfzh});
            // arr.push({label: '', payloadue: payload.mz});
            // arr.push({label: '', payloadue: moment(payload.csrq).format('YYYYMMDD')});
            // arr.push({label: '', payloadue: payload.dzxx});
            // arr.push({label: '', payloadue: payload.jgssxq});
            // arr.push({label: '', payloadue: payload.sjgjdw});
            // arr.push({label: '', payloadue: payload.ryzt});
            state.temporaryRes = payload;
        },
        setResultData (state, payload) {
            state.resultData = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
