import { cloneDeep, assign } from 'lodash';
import { submitResidencePermit } from '@/service/human/live';

const defaultState = {
    postForm: {
        sltj: '', /* ,,, */
        pcsAddress: '', /*  */
        djdz: '', /*  */
        /* {photoType,typeName,photo}
            FJLX_ID(2, ""), //1001,1002,1005
            FJLX_GF(5, ""),//1001
            FJLX_ZF(3, ""),//1002
            FJLX_ZX(4, ""),//1005
        */
        jzzPhotos: [],
        certificatePhoto: '', /*  */
        qxid: '', /* ID */
        qxmc: '', /*  */
        xzjdid: '', /* ID */
        xzjdmc: '', /*  */
        jwhid: '', /* id */
        jwhmc: '', /*  */
        /*
        * 
        * LZFS_SELF(1, ""),LZFS_EXPRESS(2, ""),
        * */
        lzfs: '', /*  */
        yjdz: '', /*  */
        sjrxm: '', /*  */
        sjrdh: ''/*  */
    },
    responseBody: {},
    liveHandleBusinessInfo: {},
    identifyPhotoList: [], // 
    reasonPhotoList: [] // 

};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setSltj (state, payload) {
            state.postForm.sltj = payload;
        },
        setSjrdh (state, payload) {
            state.postForm.sjrdh = payload;
        },
        setJzzPhotos (state, payload) {
            state.postForm.jzzPhotos = [...state.postForm.jzzPhotos, ...payload];
        },
        setIdentifyPhotoList (state, payload) {
            state.identifyPhotoList = payload;
        },
        setReasonPhotoList (state, payload) {
            state.reasonPhotoList = payload;
        },
        setMailInfo (state, payload) {
            state.postForm = { ...state.postForm, ...payload };
        },
        overridePostForm (state, payload) {
            state.postForm = { ...state.postForm, ...payload };
        },
        setResponseBody (state, payload) {
            state.responseBody = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }

    },
    actions: {
        async submitResidencePermit ({ state, commit }) {
            const res = await submitResidencePermit(state.postForm);
            if (res && res.code === '0') {
                const data = res.result;
                commit('setResponseBody', data);
                return data;
            } else if (res) {
                return res.msg || res.message || '';
            }
        }
    }
};
