// 
import { cloneDeep, assign } from 'lodash';

const defaultState = {
    mortgageInfo: {} // 
};

export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        initData (state) {
            assign(state, cloneDeep(defaultState));
        },
        // 
        setMortgageInfo (state, payload) {
            state.mortgageInfo = payload;
        }
    }
};
