import { LOCATION_ID } from '@/constant/constant';
import kttx from '@/assets/images/default/kttx.png';
import police from '@/assets/images/default/police.png';

// 
export const getServerInfoPic = (location) => {
    if (location === LOCATION_ID.bengbu ||
        location === LOCATION_ID.fanchang ||
        location === LOCATION_ID.chuzhou) {
        return kttx;
    } else {
        return police;
    }
};

// true
export const checkNeedVerify = (location) => {
    return location === LOCATION_ID.maanshan ||
        location === LOCATION_ID.dezhou ||
        location === LOCATION_ID.bengbu ||
        location === LOCATION_ID.fanchang ||
        location === LOCATION_ID.dalian ||
        location === LOCATION_ID.chuzhou ||
        location === LOCATION_ID.yinchuan ||
        location === LOCATION_ID.zhongwei;
};

// 
export const checkIsAnHuiPay = (location) => {
    return location === LOCATION_ID.hefei ||
        location === LOCATION_ID.bengbu ||
        location === LOCATION_ID.fanchang ||
        location === LOCATION_ID.dalian ||
        location === LOCATION_ID.chuzhou;
};

// 
export const mailTipMessage = (location) => {
    if (location === LOCATION_ID.bengbu) {
        return ': 202525303030';
    }
};

// 
export const isAnHuiComponents = (location) => {
    return location === LOCATION_ID.maanshan ||
        location === LOCATION_ID.bengbu ||
        location === LOCATION_ID.fanchang ||
        location === LOCATION_ID.hefei ||
        location === LOCATION_ID.chuzhou ||
        location === LOCATION_ID.yinchuan ||
        location === LOCATION_ID.zhongwei;
};

// 
export const checkNoCrimeBusiness = (location) => {
    return location === LOCATION_ID.maanshan ||
        location === LOCATION_ID.bengbu ||
        location === LOCATION_ID.fanchang ||
        location === LOCATION_ID.hefei ||
        location === LOCATION_ID.chuzhou;
};

// 
export const illegalList = (location) => {
    if (location === LOCATION_ID.yinchuan || location === LOCATION_ID.zhongwei) {
        return [
            { columnType: 'selection', width: '55' },
            { columnType: 'string', key: 'decisionNumber', label: '', width: '180' },
            { columnType: 'string', key: 'illegalTime', label: '', type: 'time' },
            { columnType: 'string', key: 'illegalPlace', label: '' },
            { columnType: 'string', key: 'wfxwnr', label: '' },
            { columnType: 'string', key: 'handleTime', label: '', type: 'time' },
            { columnType: 'string', key: 'payAmount', label: '' }
            // { columnType: 'string', key: 'lateFee', label: '' }
        ];
    } else {
        return [
            { columnType: 'selection', width: '55' },
            { columnType: 'string', key: 'decisionNumber', label: '', width: '180' },
            { columnType: 'string', key: 'illegalTime', label: '', type: 'time' },
            { columnType: 'string', key: 'illegalPlace', label: '' },
            { columnType: 'string', key: 'wfxwnr', label: '' },
            { columnType: 'string', key: 'handleTime', label: '', type: 'time' },
            { columnType: 'string', key: 'payAmount', label: '' },
            { columnType: 'string', key: 'lateFee', label: '' }
        ];
    }
};

export const illegalTypeChoose = (location) => {
    if (location === LOCATION_ID.yinchuan || location === LOCATION_ID.zhongwei) {
        return true;
    } else {
        return false;
    }
};
