import { EXIT_ENTRY_ENTRIES } from '@/constant/constant';
import moment from 'moment';
import { cloneDeep, assign } from 'lodash';

const defaultState = {
    exitAndEntryIsRepeat: false, // 
    entryAndExitReadInfo: { personInfo: {} }, // 
    entryAndExitInfo: {}, // 
    entryAndExitAvailableList: [], // 
    entryAndExitSelectList: [], // 
    entryAndExitSumFee: '', // 
    entryAndExitBusinessNum: '', // 
    entryAndExitPayResult: {},
    entryAndExitApplyInfo: {}, // 
    entryAndExitApplyStatus: '' //  '0':  '1':   '2'
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        setExitAndEntryIsRepeat (state, payload) {
            state.exitAndEntryIsRepeat = payload;
        },
        setEntryAndExitReadInfo (state, payload) {
            delete payload.msg;
            const info = {};
            info.personInfo = payload.personInfo;
            delete payload.personInfo;
            const list = Object.values(payload);
            console.log('state', payload);
            list.forEach((item) => {
                if (item.DocumentNo) {
                    item.MaxEntries = EXIT_ENTRY_ENTRIES.findIndex((time) => {
                        return time === item.MaxEntries;
                    });
                    if (item.MaxEntries === 0) {
                        item.MaxEntries = '';
                    }

                    if (item.Destination === '1') {
                        item.Destination = '';
                    } else if (item.Destination === '2') {
                        item.Destination = '';
                    } else {
                        item.Destination = '';
                    }

                    item.VisaType = item.VisaType === 'G' ? '' : '';

                    const startTime = moment(item.IssueDate).format('YYYYMMDD');
                    const endTime = moment(item.EndDate).format('YYYYMMDD');
                    item.time = startTime + '' + endTime;
                    item.time = moment(item.EndDate).unix() < moment().unix() ? item.time + '()' : item.time;
                }
            });
            info.list = list;
            console.log('', info);
            state.entryAndExitReadInfo = info;
        },
        setEntryAndExitInfo (state, payload) {
            if (payload) {
                const newList = payload.list;
                const resultList = [];
                const personInfo = cloneDeep(payload);
                delete personInfo.list;
                // 
                newList.forEach((item) => {
                    const info = resultList.find((resultItem) => {
                        return !item.destination || (resultItem.destination === item.destination);
                    });
                    if (!info) {
                        resultList.push(item);
                    }
                });
                state.entryAndExitInfo = {
                    personInfo: personInfo,
                    list: resultList
                };
            }
        },
        setEntryAndExitAvailableList (state, payload) {
            state.entryAndExitAvailableList = payload;
        },
        setEntryAndExitSelectList (state, payload) {
            state.entryAndExitSelectList = payload;
        },
        setEntryAndExitApplyInfo (state, payload) {
            state.entryAndExitApplyInfo = payload;
        },
        setEntryAndExitSumFee (state, payload) {
            state.entryAndExitSumFee = payload;
        },
        setEntryAndExitPayResult (state, payload) {
            state.entryAndExitPayResult = payload;
        },
        setEntryAndExitBusinessNum (state, payload) {
            state.entryAndExitBusinessNum = payload;
        },
        setEntryAndExitApplyStatus (state, payload) {
            state.entryAndExitApplyStatus = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
