// 
import { cloneDeep, assign } from 'lodash';

const defaultState = {
    processInfo: [] // 
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        // 
        setProcessInfo (state, payload) {
            state.processInfo = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
