//  

import { cloneDeep, assign } from 'lodash';

const defaultState = {
    number: null,
    processInfo: [] // 
};
export default {
    namespaced: true,
    state: cloneDeep(defaultState),
    mutations: {
        // 
        setProcessInfo (state, payload) {
            payload.forEach((item, index) => {
                if (item.zjzl === '21') {
                    item.zjzl = '';
                } else if (item.zjzl === '25') {
                    item.zjzl = '';
                } else if (item.zjzl === '14') {
                    item.zjzl = '';
                } else {
                    item.zjzl = '';
                }
            });
            state.processInfo = payload;
        },
        setNumber (state, payload) {
            state.number = payload;
        },
        initData (state) {
            assign(state, cloneDeep(defaultState));
        }
    }
};
